import { HeaderTitleService } from 'src/app/services/header-title.service';
import { ToastService } from 'src/app/services/toast.service';
import { Component, OnInit } from '@angular/core';
import { ApiGeneralService } from 'src/app/services/api-general.service';
import { environment } from 'src/environments/environment.prod';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-cmp',
  templateUrl: './cmp.page.html',
  styleUrls: ['./cmp.page.scss'],
})
export class CmpPage implements OnInit {

  arrSlider = [];
  AssetsPathSlider = environment.webAssetsPath + "slider-images";
  slideBannerOpts = { slidesPerView: 1, autoplay: true }

  //#region Videos Variable
  AssetsPathVideo = environment.webAssetsPath + "mobility-app/videothumbnails";
  arrAllVideoTopics = [];
  arrAllVideos = [];
  arrVideoSection1 = [];
  arrVideoSection2 = [];
  arrVideoSection3 = [];
  isVideoOpen: boolean;
  videoURL: any;
  iframe: any;
  slideVideoOpts = { slidesPerView: 1.5 }
  //#endregion

  constructor(
    private headerTitleService : HeaderTitleService,
    private apiGeneralService : ApiGeneralService,
    private toastr : ToastService,
    private sanitizer: DomSanitizer,
  ) {
    this.headerTitleService.title = "Combo Medical Program";    
   }

  ngOnInit() {
    this.getSlider();
    this.getVideos1();
  }

  //#region Slider
  getSlider() {
    let postData = { "IsMobile": true, "SliderLocation": "App-CMP" }
    this.apiGeneralService.getSlider(JSON.stringify(postData)).then((resp = []) => {
      if(resp.length){
        this.arrSlider = resp;
      }     
    })
      .catch(ex => {
        console.log("getSlider() =>" + ex);
      })
  }
  //#endregion


//#region Video
  getVideos1() { //MOKSH CEO- Combo Medical Program     
    let postData = { 'videoHeaderId': 23, 'isActive':true }
    this.apiGeneralService.getVideos(JSON.stringify(postData)).then(resp => {
      this.arrVideoSection1  = resp;
    }).catch(err => {
        console.log("getVideos3: " + err);        
      })
  }
  playVideo(videoSrc) {
    if (videoSrc == "") {
      this.toastr.toastError("Oops! Video is not available.");
      return false;
    }
    this.iframe = this.sanitizer.bypassSecurityTrustHtml('<iframe src="' + videoSrc + '" frameborder="0" style="width: 100%; height: 100%;"allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>');
    this.isVideoOpen = true;
  }
  videoPanelClose() {
    this.isVideoOpen = false;
    this.iframe = "";
  }
  //#endregion

}
